import { NgModule } from '@angular/core'

import { StoreModule }   from '@ngrx/store'
import { EffectsModule } from '@ngrx/effects'

import {
  tokenProviders as storeTokenProviders,
  buildFeatureInitialState,
  WORDPRESS_FEATURE_REDUCER,
} from './store/index'

import { featureName } from './config/index'

import {
  services,
  EntityEffects as WordpressEntityEffects,
} from './services/index'

@NgModule({
  imports: [
    StoreModule.forFeature(
      featureName,
      WORDPRESS_FEATURE_REDUCER,
      {
        initialState: buildFeatureInitialState
      }
    ),
    EffectsModule.forFeature([WordpressEntityEffects]),
  ],
  providers: [
    ...services,
    ...storeTokenProviders,
  ]
})
export class ApiModule {}
