import * as _ from 'lodash'

import {
  Injectable,
} from '@angular/core'

import {
  ApiRequestOptionsBuilder as BaseApiRequestOptionsBuilder,
  iApiResourceIdentifier,
} from '@ceo/shared'

@Injectable()
export class RequestOptionsBuilder  extends BaseApiRequestOptionsBuilder {
  protected filterParams(ri: iApiResourceIdentifier) {
    let filter = this.getFilter(ri)

    if(filter) {
      return {
        filter: this.sanitizedParams(filter)
      }
    }
    else {
      return {}
    }
  }
}
