import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  iEntityAttributes,
} from '@ceo/entity'

@Injectable({
  providedIn: 'root'
})
export class EntityAttributeBuilder {
  omittedAttributes: any[] = ["id", "type"]

  build(params: any): iEntityAttributes {
    return {
      feature: params.feature,
      type: params.type,
      id: params.id,
      attributes: this.buildAttributes(params)
    }
  }

  buildAttributes(params) {
    return _.omit(params, this.omittedAttributes)
  }
}
