import * as _ from 'lodash'

import { WordpressEntity as BaseEntity } from '../../classes'

import { WordpressEntityConfig as BaseEntityConfig } from './base-entity-config'

export function buildEntityConfig(
  params
) {

  let configType = _.get(params, 'entityConfig', BaseEntityConfig)

  let configParams = _.omit(params, ['entityConfig', 'configuration'])
  configParams = _.defaults(configParams, {entityType: BaseEntity})
  return new configType(configParams)
}


export function buildEntityConfigs(
  entityConfigParams
) {
  return _.map(
    _.sortBy(entityConfigParams, 'type'),
    buildEntityConfig
  )
}
