import * as _ from 'lodash'

import {
  HtmlMediaTagAttributes,
  iHtmlImageTagAttributes,
  iHtmlVideoTagAttributes,
  mimeTypes,
} from '@ceo/shared'

import { WordpressEntity } from './wordpress.entity'

export class AttachmentEntity extends WordpressEntity {
  static defaultMediaType: string = 'image'

  mimeType: string
  mediaDetails: any
  altText: string
  sourceUrl: string

  get htmlTagAttributes(): HtmlMediaTagAttributes {
    switch(this.htmlMediaType) { 
      case 'image': { 
        return this.imageTagAttributes()
        break; 
      } 
      case 'video': { 
        return this.videoTagAttributes()
        break; 
      } 
      default: { 
        return this.imageTagAttributes()
        break; 
      } 
    } 
  }

  get htmlMediaType(): string {
    let mimeType = _.find(mimeTypes, { mimeType: this.mimeType }) as any
    return mimeType ? mimeType.mediaType : AttachmentEntity.defaultMediaType
  }

  private imageTagAttributes(): iHtmlImageTagAttributes {
    return {
      width: this.mediaDetails.width,
      height: this.mediaDetails.height,
      alt: this.altText,
      src: this.sourceUrl,
    }
  }

  private videoTagAttributes(): iHtmlVideoTagAttributes {
    return {
      autoplay: false,
      controls: true,
      width: this.mediaDetails.width,
      height: this.mediaDetails.height,
      src: [{
        src: this.sourceUrl,
        type: this.mimeType,
      }]
    }
  }
}
